.Toolbar {
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    box-shadow: 0px 0px 10px 0 rgba(0, 0, 0, 0.2);
    z-index: 90;
}

.Toolbar nav {
    height: 100%;

}

@media (max-width: 499px) {
    .DesktopOnly {
        display: none;
    }
}